import {newPreview} from '../../../utils/utils.js'
import uniSteps from '@/components/uni-steps/uni-steps.vue'
export default {
	components: {uniSteps},
	data() {
		return {
				picUrl:[]
		}
	},
	methods: {
		downloadPPT() {
			var self = this;
			uni.getSavedFileList({
			  success: function (res) {
				  console.log("文件列表")
				  console.log(res)
			    res.fileList.forEach(item=>{
					uni.removeSavedFile({
					  filePath: item.filePath,
					  complete: function (res) {
						console.log(res);
					  }
					});
				})
			  }
			});
			
			wx.downloadFile({
				url:'https://minimalist.net.cn'+self.$store.state.productInfo.filePath,
				success(res){
					console.log(res);
					uni.saveFile({
					  tempFilePath:res.tempFilePath,
					  success: function (res) {
					    var savedFilePath = res.savedFilePath;
						console.log("本地文件");
						console.log(savedFilePath);
						console.log("打开文件")
						wx.openDocument({
							filePath:savedFilePath,
							fileType:'pptx,',
							showMenu:true
						})
					  },
					  fail:function (res) {
						  console.log("文件没有保存成功");
						  console.log(res);
						  
					  },
					  
					});
					
				},
				fail(res){
					console.log('https://minimalist.net.cn'+self.$store.state.productInfo.filePath);
					console.log(res);
				}
			})
		}
	},
	onLoad(options) {
		console.log('这里是预览页');
		newPreview(this.$store.state.productInfo.fileId).then(res=>{
			console.log(res);
			this.picUrl = res.data;
		})
		 var that = this
		 console.log("查看页面栈");
		console.log(getCurrentPages());
		        //判断页面栈里面的页面数是否大于2
		        if(getCurrentPages().length > 2) {
		            //获取页面栈
		            let pages = getCurrentPages()
		            //给上一个页面设置状态
		            let curPage = pages[pages.length - 2];
		            let data = curPage.data;
		            curPage.setData({'isBack': true});
		        }
		
	},
}
